@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css);
:root {
  --primary-color: #047aed;
  --secondary-color: #1c3fa8;
  --dark-color: #002240;
  --light-color: #f4f4f4;
  --success-color: #5cb85c;
  --error-color: #d9534f;
}

* {
  scroll-behavior: smooth; /* chrome */
  -webkit-overflow-scrolling: touch; /* safari */
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Lato", sans-serif;
  color: #333;
  line-height: 1.6;
}

.section-header {
  position: relative;
  font-size: 1.5rem;
  width: 100%;
  padding: 0.5em;
  background-color: grey;
}

.section-menu{

}

.section-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2%;
  width: 100%;
}

.section-login {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.section-userinfo {
  width: 30%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
}

li {
    float: left;
}

li .link {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

li .link:hover{
    background-color: #111;
}

.link-signout{
    float: right;
}

.btn-signout{
    margin-top: 0.5em;
    height: 40px;
}
/* Clear component */

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #333;
}

h1,
h2 {
  font-weight: 300;
  line-height: 1.2;
  margin: 10px 0;
}

p {
  margin: 10px 0;
}

img {
  width: 100%;
}

code,
pre {
  background-color: #333;
  color: #fff;
  padding: 10px;
}

/* Component */

.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: auto;
  padding: 0 2.5rem;
}

section {
  margin-top: 1em;
}

.card {
  background-color: #fff;
  color: #333;
  border-radius: 0.6em;
  box-shadow: 0 3px 0.6em rgba(0, 0, 0, 0.2);
  padding: 1em;
  margin: 1em;
}

.button {
  max-width: 250px;
  display: inline-block;
  padding: 0.6rem 1.8rem;
  cursor: pointer;
  background: var(--primary-color);
  color: #fff;
  border: none;
  border-radius: 5px;
  outline: none;
}

.textbox {
  height: 1.5em;
  width: 100%;
  outline: none;
  font-size: 18px;
  font-family: inherit;
  border: 0.1px inset rgba(0, 0, 0, 0.1);
}

.button-outline {
  background: transparent;
  border: 1px #fff solid;
}

.button:hover {
  transform: scale(0.98);
}

.popup{
  position: absolute;
  max-width: 80%;
  margin: 0 auto;
  left: 0;
  right: 0;
  background-color: #fff;
  border: 2px solid #000;
  box-shadow: rgba(0, 0, 0, 0.2);
  padding: 4px;
}

.bg-primary,
.button-primary {
  background-color: var(--primary-color);
}

.bg-dark,
.button-dark {
  background-color: var(--dark-color);
  color: #fff;
}

.bg-light,
.button-light {
  background-color: var(--light-color);
  color: #333;
}

.bg-primary a,
.button-primary a,
.bg-secondary a,
.button-secondary a,
.bg-dark a,
.button-dark a {
  color: #fff;
}

.text-primary {
  color: var(--primary-color);
}

.text-secondary {
  color: var(--secondary-color);
}

.text-dark {
  color: var(--dark-color);
}

.text-light {
  color: var(--light-color);
}

.lead {
  font-size: 1.25rem;
}

.underline {
  padding-bottom: 1rem;
  border-bottom: solid 1px var(--primary-color);
}

.sm {
  font-size: 1rem;
}

.md {
  font-size: 2rem;
}

.lg {
  font-size: 3rem;
}

.xl {
  font-size: 4rem;
}

.text-center {
  text-align: center;
}

.text-weight-bold {
  font-weight: bolder;
}

.alert {
  background-color: var(--light-color);
  padding: 0.6rem 1.25rem;
  font-weight: bold;
  margin: 15px 0;
}

.alert i {
  margin-right: 0.6rem;
}

.alert-success {
  background-color: var(--success-color);
  color: #fff;
}

.alert-error {
  background-color: var(--error-color);
  color: #fff;
}

/* Layout */
.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.flex-row {
  flex-direction: row;
}

.flex-column {
  flex-direction: column;
}

.flex-space-between {
  justify-content: space-between;
  align-items: stretch;
}

.flex-start {
  justify-content: start;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.grid-1 {
  grid-template-columns: repeat(1, 1fr);
}

.grid-3 {
  grid-template-columns: repeat(3, 1fr);
}

.grid-4 {
  grid-template-columns: repeat(4, 1fr);
}

.grid-5 {
  grid-template-columns: repeat(5, 1fr);
}

